<template>
  <q-page>
    <div v-if="!isBackendAvailable">
      <backend-status></backend-status>
    </div>

    <instrument-control v-if="isBackendAvailable"></instrument-control>
  </q-page>
</template>

<script>

import InstrumentControl  from 'src/components/InstrumentControl.vue';
import BackendStatus from '@/components/BackendStatus.vue';
import { useInstrumentStore } from '@/stores/instruments-store';
import { computed } from 'vue'
import NoSleep from 'nosleep.js'

export default {
  setup() {
    const store = useInstrumentStore();
    const isBackendAvailable = computed(() => store.isBackendAvailable);
    
    return { 
      isBackendAvailable,
      store
    };

  },
  data() {
    return {
      noSleep:new NoSleep(),
    };
  },
  components: {
    InstrumentControl,
    BackendStatus,
  },
  mounted() {
    if (this.store.isPreventScreenDimming) {
      console.log('Enable prevent screen dimming');
      this.noSleep.enable();
    }
  },
  unmounted() {
    if (this.store.isPreventScreenDimming) {
      console.log('Disable prevent screen dimming');
      this.noSleep.disable();
    }
  },

}
</script>