<template>
  <q-card flat>
    <q-card-section class="q-pb-xl">
      <q-btn color='secondary' class="q-mt-sm" no-caps rounded @click="selectInstrument">{{ selectedInstrumentName || "Wähle dein Instrument"}}</q-btn>
      <q-fab v-if='selectedInstrumentId && !isMuted' class='float-right' color="primary"  icon="keyboard_arrow_left" direction="left">
          <q-fab-action  color='secondary' @click="toggleMute" icon="volume_off">
          </q-fab-action>
          <q-fab-action  color='secondary' @click="openPanorama" icon="panorama_horizontal">
          </q-fab-action>
          <q-fab-action  color='secondary' v-if='selectedInstrumentHasTuner' @click="openTuner">
            <img class='icon-img' src="@/assets/icons/tuning-fork.png">
          </q-fab-action>
        
      </q-fab>
    </q-card-section>

    <q-card-section v-if='selectedInstrumentId && !isMuted' flat class="full-width q-pa-none row no-wrap items-start q-pa-none">
      <div class="col-5">
        <q-card flat>
          <q-card-section class="q-mr-sm">
            <div class="full-width q-mb-md flex flex-center">
              <q-btn :disable='isMuted' round icon="volume_up" color='primary' @click='changeVolume(1)' size="lg"></q-btn>
            </div>
            
            <div class="full-width flex flex-center">
              <q-slider
                :disable="isMuted"
                class='vertical-slider'
                v-model="intVolume"
                track-size="40px"
                thumb-size="0px"
                color="grey-6"
                reverse
                :min="0"
                :max="100"
                vertical
              />
            </div>
            <div class="full-width q-mt-md q-mb-xl flex flex-center">
              <q-btn :disable='isMuted' round icon="volume_down" color='primary' @click='changeVolume(-1)' size="lg"></q-btn>
            </div>
            <div class='full-width flex flex-center'>
              {{  displayVolume }}
            </div>
          </q-card-section>
        </q-card>
      </div>

      <div class="col-7">
        <q-card flat>
          <q-card-section class="q-pl-md q-pr-lg">
            <q-btn padding=''
                      :disable="isMuted"
                      dense
                      no-wrap
                      no-caps size='md' 
                      class='full-width q-mb-md q-pa-md' 
                      :class="getPresetButtonClass(index+1)"
                      :label="presets[index]"
                      v-for="(preset, index) in presets" :key="index"
                      @click="selectPreset(index + 1)">
              </q-btn>
          </q-card-section>
        </q-card>
      </div>

    </q-card-section>

    <q-card-section v-if='selectedInstrumentId && isMuted' flat class="column items-center" style="margin-top: 120px;">
          <q-btn round icon="volume_off" color='primary' @click='toggleMute' size="xl"></q-btn>
          <div class="q-mt-lg">
          Instrument ist stummgeschaltet
          </div>
    </q-card-section>

  </q-card>
</template>

<script>
import { useInstrumentStore } from '@/stores/instruments-store';
import { useRouter } from 'vue-router';
import { computed, watch } from 'vue';
import { getInstrumentById, selectInstrumentPreset, toggleInstrumentMute, updateInstrumentVolume } from '@/services/apiServices';

export default {
setup() {
  const store = useInstrumentStore();
  const router = useRouter();

  const selectedInstrumentName = computed(() => store.getSelectedInstrumentName);
  const selectedInstrumentId = computed(() => store.getSelectedInstrumentId);
  const selectedInstrumentHasTuner = computed(() => store.selectedInstrumentHasTuner);

  const selectInstrument = () => {
    router.push('/select-instrument');
  }

  const openTuner = () => {
    router.push(`/tuner/${selectedInstrumentId.value}`);
  }

  const openPanorama = () => {
    router.push(`/panorama/${selectedInstrumentId.value}`);
  }

  return { 
    selectedInstrumentName,
    selectedInstrumentId,
    selectedInstrumentHasTuner,
    selectInstrument,
    openTuner,
    openPanorama,
  };
},
created() {
  const store = useInstrumentStore();

  this.updateInstrumentState(store.getSelectedInstrumentId)
},

name: 'InstrumentControl',
data() {
  return {
    isLoading: true,
    volume: 30,
    isMuted: false,
    currentPreset: null,       // we start with index 0, in the REST api the first element is 1
    presets: [],
  };
},
components:
{
},
computed: {
  intVolume: {
    get() {
      return this.volume;
    },
    set(value) {
      let volume = parseInt(value, 10);

      this.changeVolume(volume - this.volume);
    }
  },
  displayVolume: {
    get() {
      return "Lautstärke " + this.volume + ' %';
    }
  } 
},
mounted() {
  const store = useInstrumentStore();

  watch(() => store.selectedInstrumentId, (newValue) => {
    this.updateInstrumentState(newValue);
  });
  
},
methods: {
  getPresetButtonClass(index) {
    if (this.currentPreset === index) {
      return 'bg-primary text-white';
    } else 
    {
      return 'bg-secondary text-white';
    }
  },
  getMuteButtonClass() {
      return {
        'bg-primary text-white': this.isMuted
      };
  },
  updateInstrumentState(selectedInstrumentId) {
    if (!selectedInstrumentId) return;

    getInstrumentById(selectedInstrumentId)
      .then(response => {
        this.isMuted = response.data.is_muted;
        this.volume = response.data.volume;
        this.currentPreset = response.data.selected_preset.id;

        // retrieve reported presets and store names in local array
        this.presets = response.data.presets.slice(0, 6).map(preset => preset.name);
      })
      .catch(error => {
        console.error('Error fetching instrument state:', error);
      });

      this.isLoading = false;
  },
  toggleMute() {
    const store = useInstrumentStore();
    const selectedInstrumentId = store.getSelectedInstrumentId;
    if (!selectedInstrumentId) return;

    this.isMuted = !this.isMuted;

    toggleInstrumentMute(selectedInstrumentId)
      .then(response => {
        this.isMuted = response.data.is_muted
      })
      .catch(error => {
        console.error('Error toggling mute:', error);
      });

  },
  changeVolume(amount) {
    const store = useInstrumentStore();
    const selectedInstrumentId = store.getSelectedInstrumentId;
    if (!selectedInstrumentId) return;

    this.volume = Math.max(0, Math.min(100, this.volume + amount));

    // perform REST api call and update state with response
    updateInstrumentVolume(selectedInstrumentId, this.volume)
      .then(response => {
        this.volume = response.data.volume
      })
      .catch(error => {
        console.error('Error setting volume:', error);
      });


  },
  selectPreset(presetId) {
    const store = useInstrumentStore();
    const selectedInstrumentId = store.getSelectedInstrumentId;
    if (!selectedInstrumentId) return;

    this.currentPreset = presetId;

    selectInstrumentPreset(selectedInstrumentId, presetId)
      .then(response => {
        this.currentPreset = response.data.selected_preset.id;
      })
      .catch(error => {
        console.error('Error setting preset:', error);
      });

  }
}
};
</script>

<style scoped>
.icon-img {
  width: 24px;
  height: 24px;
  max-width: 100%;
  max-height: 100%;
}

</style>